import { createSelector } from 'reselect';
import { STORE_KEY } from './constants';
import { State } from './reducer';
import { AppState } from 'reducers';

/**
 * Direct selector to the messagesPage state domain
 */
const selectMessagesPageDomain = () => (state: AppState): State =>
  state[STORE_KEY];

/**
 * Other specific selectors
 */

const makeSelectIsFetchingCurrentTopics = () =>
  createSelector(
    selectMessagesPageDomain(),
    (substate) => substate.isFetchingCurrentTopics,
  );

const makeSelectCurrentTopics = () =>
  createSelector(
    selectMessagesPageDomain(),
    (substate) => substate.currentTopics,
  );

const makeSelectCurrentTopicsCount = () =>
  createSelector(
    selectMessagesPageDomain(),
    (substate) => substate.totalTopicsCount,
  );

const makeSelectSearchedTopic = () =>
  createSelector(
    selectMessagesPageDomain(),
    (substate) => substate.searchedTopic,
  );

const makeSelectIsFetchingSearchedTopic = () =>
  createSelector(
    selectMessagesPageDomain(),
    (substate) => substate.isFetchingSearch,
  );

const makeSelectIsFetchingTopicNames = () =>
  createSelector(
    selectMessagesPageDomain(),
    (substate) => substate.isFetchingTopicNames,
  );

const makeSelectAllTopicNames = () =>
  createSelector(
    selectMessagesPageDomain(),
    (substate) => substate.allTopicNames,
  );
/**
 * Default selector used by MessagesPage
 */

const makeSelectMessagesPage = () =>
  createSelector(selectMessagesPageDomain(), (substate) => substate);

export default makeSelectMessagesPage;
export {
  selectMessagesPageDomain,
  makeSelectIsFetchingCurrentTopics,
  makeSelectCurrentTopics,
  makeSelectSearchedTopic,
  makeSelectIsFetchingSearchedTopic,
  makeSelectIsFetchingTopicNames,
  makeSelectAllTopicNames,
  makeSelectCurrentTopicsCount,
};



