/*
 *
 * MessagesPage
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import Helmet from 'react-helmet';
import { FormattedMessage, injectIntl } from 'react-intl';
import { Button } from 'reactstrap';
import injectSaga from 'utils/injectSaga';
import { compose } from 'redux';
import injectReducer from 'utils/injectReducer';
import {
  makeSelectSearchedTopic,
  makeSelectIsFetchingSearchedTopic,
  makeSelectAllTopicNames,
  makeSelectIsFetchingCurrentTopics,
  makeSelectIsFetchingTopicNames,
  makeSelectCurrentTopics,
  makeSelectCurrentTopicsCount,
} from './selectors';
import reducer from './reducer';
import { STORE_KEY } from './constants';
import saga from './sagas';
import messages from './messages';
import { CBDispatch } from 'cb-utils/typeUtils';
import ConsolePage from 'components/ConsolePage';
import CbDropDown from 'components/CbDropDown';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import { GearDropDown } from 'containers/EdgesPage';
import { CaretDownIcon, MessagesIcon, RolesIcon } from 'components/EntityIcons';
import {
  createFetchTopicRequest,
  createFetchCurrentTopicsRequest,
  createFetchTopicNamesRequest,
} from './actions';
import { createShowBaseModal } from 'containers/Console/actions/ui';
import { CustomModalBodyComponents } from 'components/BaseModal';
import MqttConnection from 'containers/MqttConnection';
import PageHeader from 'components/ConsolePageHeader';
import { CBEvent } from 'utils/types';
import Tabs from 'components/Tabs';
import RecentMessagesTab from 'components/RecentMessagesTab';
import LiveMessageContainer from 'containers/LiveMessageContainer';
import { createStructuredSelector } from 'reselect';

interface MessagesPageProps extends SystemRouteProps<{}> {}

export type MasterProps = MessagesPageProps &
  DispatchToProps &
  StateToProps &
  ReactIntl.InjectedIntlProps;

export class MessagesPage extends React.PureComponent<MasterProps, {}> {
  render() {
    const isEdge = !!this.props.match.params.edgeName;
    return (
      <ConsolePage subheader={this.renderSubheader(isEdge)} allowScroll={false}>
        <Helmet
          title="ClearBlade - Messages"
          meta={[
            { name: 'description', content: 'Description of MessagesPage' },
          ]}
        />
        <React.Fragment>
          {isEdge ? (
            <RecentMessagesTab {...this.props} />
          ) : (
            <Tabs tabs={this.renderTabs()} useRoutes />
          )}
        </React.Fragment>
      </ConsolePage>
    );
  }

  renderTabs = () => {
    return [
      {
        id: 'recentTopics',
        title: this.props.intl.formatMessage(messages.recentTopics),
        component: <RecentMessagesTab {...this.props} />,
      },
      {
        id: 'test',
        title: this.props.intl.formatMessage(messages.testTopic),
        component: (
          <LiveMessageContainer allTopicNames={this.props.allTopicNames} />
        ),
      },
    ];
  };

  header = (
    <div>
      <MessagesIcon /> <FormattedMessage {...messages.header} />
      <CbDropDown
        anchorEl={<CaretDownIcon /> as any}
        triggerEl={<GearDropDown />}
        items={[
          {
            displayName: (
              <div>
                <RolesIcon /> <FormattedMessage {...messages.editRoles} />
              </div>
            ),
            onClick: () =>
              this.props.showBaseModal('EditRolesModal', {
                assetClass: 'messages',
              }),
          },
        ]}
      />
    </div>
  );

  renderSubheader = (isEdge: boolean) => {
    return (
      <PageHeader
        header={this.header}
        actionButton={
          !isEdge
            ? () => (
                <Button
                  onClick={(e) => {
                    e.preventDefault();
                    this.props.showBaseModal('MessagesModal', {});
                  }}
                  color="primary"
                  data-cy="open-message-modal"
                >
                  <FormattedMessage {...messages.sendMessage} />
                </Button>
              )
            : null
        }
        subHeader={!isEdge ? <MqttConnection /> : null}
      />
    );
  };

  updateSearchField = (e: CBEvent) => {
    this.setState({
      ...this.state,
      search: e.target.value,
    });
  };
}

type StateToProps = ReturnType<typeof mapStateToProps>;
const mapStateToProps = createStructuredSelector({
  isFetchingCurrentTopics: makeSelectIsFetchingCurrentTopics(),
  currentTopics: makeSelectCurrentTopics(),
  currentTopicsCount: makeSelectCurrentTopicsCount(),
  searchedTopic: makeSelectSearchedTopic(),
  isFetchingSearchedTopic: makeSelectIsFetchingSearchedTopic(),
  isFetchingTopicNames: makeSelectIsFetchingTopicNames(),
  allTopicNames: makeSelectAllTopicNames(),
});

type DispatchToProps = ReturnType<typeof mapDispatchToProps>;
function mapDispatchToProps(dispatch: CBDispatch) {
  return {
    fetchCurrentTopics: (isInitialized: boolean, query: QueryObj) =>
      dispatch(createFetchCurrentTopicsRequest(isInitialized, query)),
    fetchTopicNames: () => dispatch(createFetchTopicNamesRequest()),
    fetchTopic: (topicName: string) =>
      dispatch(createFetchTopicRequest(topicName)),
    showBaseModal: (component: CustomModalBodyComponents, props: object) =>
      dispatch(createShowBaseModal({ component, props })),
  };
}

const withConnect = connect(mapStateToProps, mapDispatchToProps);

const withReducer = injectReducer({ key: STORE_KEY, reducer });
const withSaga = injectSaga({ key: STORE_KEY, saga });
export default compose(withReducer, withSaga, withConnect, injectIntl)(
  MessagesPage,
);



