import { all, takeEvery, call, put, select } from 'redux-saga/effects';
import { ActionConstants } from './actionConstants';

import {
  FetchTopicRequest,
  createFetchTopicSuccess,
  createFetchCurrentTopicsSuccess,
  FetchCurrentTopicsRequest,
  createFetchTopicNamesSuccess,
  createFetchCurrentTopicsRequest,
} from 'containers/MessagesPage/actions';
import {
  getCurrentTopics,
  getMessageHistory,
  getAllMessageTopics,
  getCurrentTopicsCount,
} from 'cb-utils/services/messages';
import {
  createNewNotification,
  createDismissNotification,
} from 'containers/Console/actions/ui';
import { MessageModel } from 'cb-utils/console-entity-models';
import { makeSelectCurrentTopics } from './selectors';
import { selectNotifications } from '../Console/selectors/ui';
import store from 'appStore';
import { NotificationObject } from 'react-notification';

function* callFetchCurrentTopics(action: FetchCurrentTopicsRequest) {
  try {
    const recentTopics = yield call(getCurrentTopics, {
      query: action.payload.query,
    });
    const totalTopicsCount = yield call(getCurrentTopicsCount);
    if (action.payload.isInitialized) {
      const newMessagesArrived = yield call(checkForNewMessages, recentTopics);
      if (newMessagesArrived) {
        const notifications = yield select(selectNotifications());
        const existingNotification = notifications.filter(
          (not: NotificationObject) => not.title === 'New messages received',
        );
        if (existingNotification.length === 0) {
          yield put(
            createNewNotification({
              title: 'New messages received',
              action: 'Click to refresh recent topics',
              dismissAfter: 10000000,
              onClick: (notification, deactivate) => {
                store.dispatch(
                  createFetchCurrentTopicsRequest(false, action.payload.query),
                );
                deactivate();
                // @ts-ignore - react-notification wants this to be NotificationProps interface which
                // does not include the key, which is needed. We use NotificationObject in our interaction
                store.dispatch(createDismissNotification(notification));
              },
            }),
          );
        }
      }
    } else {
      yield put(
        createFetchCurrentTopicsSuccess(recentTopics, totalTopicsCount),
      );
    }
  } catch (e) {
    yield put(
      createNewNotification({
        title: 'Error',
        message: 'Could not fetch recent topics',
        className: 'error',
      }),
    );
  }
}

function* callFetchTopicNames() {
  try {
    const topicNames = yield call(getAllMessageTopics);
    yield put(createFetchTopicNamesSuccess(topicNames));
  } catch (e) {
    yield put(
      createNewNotification({
        title: 'Error',
        message: 'Could not fetch all topic names for search',
        className: 'error',
      }),
    );
  }
}

function* checkForNewMessages(messages: MessageModel[]) {
  const currentMessages: MessageModel[] = yield select(
    makeSelectCurrentTopics(),
  );
  if (
    messages.length > currentMessages.length ||
    (messages[0] && messages[0].pk !== currentMessages[0].pk)
  ) {
    return true;
  }
  return false;
}

function* callFetchSearchedTopic(action: FetchTopicRequest) {
  if (action.payload.topicName.length > 0) {
    try {
      const date = new Date();
      const stringParams = `topic=${
        action.payload.topicName
      }&last=${date.getTime()}&count=1`;
      const message: MessageModel[] = yield call(
        getMessageHistory,
        stringParams,
      );
      if (message.length > 0) {
        yield put(createFetchTopicSuccess(message as MessageModel[]));
      } else {
        yield put(
          createNewNotification({
            title: `Could not find most recent message for ${
              action.payload.topicName
            }`,
            className: 'error',
          }),
        );
      }
    } catch (e) {
      yield put(
        createNewNotification({
          title: 'Error',
          message: `Could not fetch topic ${action.payload.topicName}`,
          className: 'error',
        }),
      );
    }
  }
}

export function* fetchSearchedTopic() {
  yield takeEvery(ActionConstants.FETCH_TOPIC_REQUEST, callFetchSearchedTopic);
}

export function* fetchCurrentTopicsSaga() {
  yield takeEvery(
    ActionConstants.FETCH_CURRENT_TOPICS_REQUEST,
    callFetchCurrentTopics,
  );
}

export function* fetchTopicNamesSaga() {
  yield takeEvery(
    ActionConstants.FETCH_TOPIC_NAMES_REQUEST,
    callFetchTopicNames,
  );
}

// All sagas to be loaded
export default function*() {
  yield all([
    fetchSearchedTopic(),
    fetchCurrentTopicsSaga(),
    fetchTopicNamesSaga(),
  ]);
}



