/*
 *
 * MessagesPage reducer
 *
 */

import assign from 'immutable-assign';
import { ActionTypes } from './actions';
import { ActionConstants } from './actionConstants';
import {
  MessageModel,
  CurrentTopicsModel,
} from 'cb-utils/console-entity-models';

export interface State {
  isFetchingCurrentTopics: boolean;
  isFetchingTopicNames: boolean;
  isFetchingSearch: boolean;
  searchedTopic?: MessageModel[];
  currentTopics: CurrentTopicsModel[];
  allTopicNames: string[];
  totalTopicsCount: number;
}

export const initialState: State = {
  isFetchingCurrentTopics: false,
  isFetchingTopicNames: false,
  isFetchingSearch: false,
  searchedTopic: null,
  currentTopics: [],
  allTopicNames: [],
  totalTopicsCount: null,
};

function messagesPageReducer(state: State = initialState, action: ActionTypes) {
  switch (action.type) {
    case ActionConstants.DEFAULT_ACTION:
      return state;
    case ActionConstants.FETCH_CURRENT_TOPICS_REQUEST:
      return assign(state, (m) => {
        m.isFetchingCurrentTopics = action.payload.isInitialized ? false : true;
        return m;
      });
    case ActionConstants.FETCH_CURRENT_TOPICS_SUCCESS:
      return assign(state, (m) => {
        m.currentTopics = action.payload.currentTopics;
        m.totalTopicsCount = action.payload.totalTopicsCount;
        m.isFetchingCurrentTopics = false;
        return m;
      });
    case ActionConstants.FETCH_TOPIC_NAMES_REQUEST:
      return assign(state, (m) => m.isFetchingTopicNames, () => true);
    case ActionConstants.FETCH_TOPIC_NAMES_SUCCESS:
      return assign(state, (m) => {
        m.allTopicNames = action.payload.topicNames;
        m.isFetchingTopicNames = false;
        return m;
      });
    case ActionConstants.FETCH_TOPIC_REQUEST:
      return assign(state, (m) => {
        m.isFetchingSearch = true;
        m.searchedTopic = null;
        return m;
      });
    case ActionConstants.FETCH_TOPIC_SUCCESS:
      return assign(state, (m) => {
        m.searchedTopic = action.payload.message;
        m.isFetchingSearch = false;
        return m;
      });
    default:
      return state;
  }
}

export default messagesPageReducer;



