/**
 *
 * MessageTopicFetch
 *
 */

import React from 'react';
import styled from 'styled-components';
import { InputGroup } from 'reactstrap';
import { FormattedMessage, injectIntl } from 'react-intl';
import messages from './messages';
import CbTypeahead from 'components/CbTypeahead';
import { MessageModel, MessageColumns } from 'cb-utils/console-entity-models';
import MessageHistoryGrid from 'components/MessageHistoryGrid';
import { CustomModalBodyComponents } from 'components/BaseModal';

const InputWrapper = styled(InputGroup)`
  > :first-child {
    width: 100% !important;
  }
`;

interface MessageTopicFetchProps {
  allTopics: string[];
  fetchTopic: (name: string) => void;
  searchedTopic: MessageModel[];
  fetchTopicNames: () => void;
  isFetchingTopicNames: boolean;
  isFetchingSearchedTopic: boolean;
  showModal: (component: CustomModalBodyComponents, props: object) => void;
}

class MessageTopicFetch extends React.Component<
  MessageTopicFetchProps & ReactIntl.InjectedIntlProps
> {
  // eslint-disable-line react/prefer-stateless-function
  render() {
    return (
      <div className="mt-4">
        <h4>
          <FormattedMessage {...messages.fetchTopic} />
        </h4>
        <InputWrapper>
          <CbTypeahead
            options={this.props.allTopics.map((topic) => {
              return {
                name: topic,
                value: topic,
              };
            })}
            onFocus={this.props.fetchTopicNames}
            onChange={this.props.fetchTopic}
            emptyLabel={this.props.intl.formatMessage(messages.chooseTopic)}
          />
        </InputWrapper>
        {this.props.searchedTopic ? (
          <MessageHistoryGrid
            data={this.props.searchedTopic}
            isLoading={this.props.isFetchingSearchedTopic}
            isSearch
            columnNames={[
              MessageColumns.TOPIC_ID,
              MessageColumns.MESSAGE,
              MessageColumns.SEND_DATE,
              MessageColumns.USER_ID,
              MessageColumns.RESEND,
            ]}
            showModal={this.props.showModal}
          />
        ) : (
          <div className="text-muted">Search for any topic above</div>
        )}
      </div>
    );
  }
}

export default injectIntl(MessageTopicFetch);



