/*
*
* RecentMessagesTab
*
*/

import React from 'react';
import styled from 'styled-components';

import { FormattedMessage } from 'react-intl';
import messages from './messages';
import MessageTopicFetch from 'components/MessageTopicFetch';
import { MasterProps } from 'containers/MessagesPage';
import CrudGrid from 'components/CrudGridContainer';
import { CurrentTopicsColumns } from 'cb-utils/console-entity-models';
import { Column } from 'react-table';
import historyGridMessages from 'components/MessageHistoryGrid/messages';
import mainPageMessages from 'containers/MessagesPage/messages';
import { Link } from 'react-router-dom';
import { format } from 'date-fns';
import { cb } from 'cb-utils/cbLib';
import CopyInput from '../CopyInput';

const GridsWrapper = styled.div`
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
`;

let timer: NodeJS.Timer;

const columnNames = [
  CurrentTopicsColumns.TOPIC_ID,
  CurrentTopicsColumns.PAYLOAD,
  CurrentTopicsColumns.TIME,
  CurrentTopicsColumns.USER_ID,
  CurrentTopicsColumns.PAYLOAD_SIZE,
  CurrentTopicsColumns.RESEND,
];

interface RecentMessagesState {
  query: QueryObj;
}

export class RecentMessagesTab extends React.Component<
  MasterProps,
  RecentMessagesState
> {
  state: RecentMessagesState = {
    query: cb.Query().setPage(25, 1),
  };

  componentDidMount() {
    timer = setInterval(
      () => this.props.fetchCurrentTopics(true, this.state.query),
      10000,
    );
  }

  componentWillUnmount() {
    clearInterval(timer);
  }

  render() {
    return (
      <GridsWrapper>
        <MessageTopicFetch
          allTopics={this.props.allTopicNames}
          fetchTopic={this.props.fetchTopic}
          searchedTopic={this.props.searchedTopic}
          fetchTopicNames={this.props.fetchTopicNames}
          isFetchingTopicNames={this.props.isFetchingTopicNames}
          isFetchingSearchedTopic={this.props.isFetchingSearchedTopic}
          showModal={this.props.showBaseModal}
        />
        <h4 className="mt-4">
          <FormattedMessage {...mainPageMessages.recentTopics} />
        </h4>
        <CrudGrid
          rows={this.props.currentTopics}
          columns={this._mapColumns()}
          columnNames={columnNames}
          count={this.props.currentTopicsCount}
          refresh={this._fetchData}
          isLoading={this.props.isFetchingCurrentTopics}
          onFetchData={this._fetchData}
          pkColumn={CurrentTopicsColumns.MESSAGE_ID}
          transformColumns={this._transformColumns}
        />
      </GridsWrapper>
    );
  }

  _mapColumns = () => {
    return columnNames.map((col) => {
      return {
        ColumnName: col,
        ColumnType: null,
        PK: col === CurrentTopicsColumns.MESSAGE_ID,
      };
    });
  };

  _fetchData = (query: QueryObj) => {
    const clone = JSON.parse(JSON.stringify(query));
    delete clone.query.SORT;
    this.setState({
      query: clone,
    });
    this.props.fetchCurrentTopics(false, clone);
  };

  _transformColumns = (columns: Column[]) => {
    const { intl: { formatMessage } } = this.props;
    const newCols = columns.map((col) => {
      const colName = col.accessor;
      switch (colName) {
        case CurrentTopicsColumns.TOPIC_ID:
          col.Header = <FormattedMessage {...historyGridMessages.topic} />;
          col.Cell = (cellInfo) => {
            const val = cellInfo.row[colName];
            return <Link to={`${this.props.match.url}/${val}`}>{val}</Link>;
          };
          col.maxWidth = 400;
          return col;
        case CurrentTopicsColumns.TIME:
          col.Header = <FormattedMessage {...historyGridMessages.received} />;
          col.Cell = (cellInfo) => {
            return (
              cellInfo.value &&
              format(new Date(cellInfo.value * 1000), `MMM DD YYYY, hh:mm a`)
            );
          };
          col.maxWidth = 190;
          return col;
        case CurrentTopicsColumns.PAYLOAD_SIZE:
          col.Header = `${formatMessage(messages.payloadSize)} (${formatMessage(
            messages.bytes,
          )})`;
          col.Cell = (cellInfo) => cellInfo.value;
          col.maxWidth = 190;
          return col;
        case CurrentTopicsColumns.PAYLOAD:
          col.Header = (
            <FormattedMessage {...historyGridMessages.lastMessage} />
          );
          col.Cell = (cellInfo) => cellInfo.value;
          return col;
        case CurrentTopicsColumns.USER_ID:
          col.Header = <FormattedMessage {...historyGridMessages.userId} />;
          col.Cell = (cellInfo) => (
            <CopyInput value={cellInfo.value} isGridCell />
          );
          return col;
        case CurrentTopicsColumns.RESEND:
          col.Header = '';
          col.Cell = (rowInfo) => {
            return (
              <a
                href=""
                data-cy="message-resend-link"
                onClick={(e) => {
                  e.preventDefault();
                  this.props.showBaseModal('MessagesModal', {
                    message: rowInfo.original,
                    isDetail: false,
                    query: this.state.query,
                  });
                }}
                id="resend-message"
              >
                <FormattedMessage {...historyGridMessages.view} />
              </a>
            );
          };
          col.maxWidth = 120;
          return col;
        default:
          return col;
      }
    });
    return newCols;
  };
}

export default RecentMessagesTab;



